import React, { ReactElement } from "react";
import {
  Container,
  createTheme,
  CssBaseline,
  Divider,
  Paper,
  TextField,
  ThemeProvider,
  Typography,
} from "@mui/material";
import { useParams } from "react-router-dom";
import { usePropertyFetch } from "../hooks/PropertyFetch";
import { useForm, Controller, Control } from "react-hook-form";
import { BASE_API_URL } from "../util/Constants";

const defaultTheme = createTheme();

function FormInputText(props: {
  name: string;
  control: Control;
  label: string;
}): ReactElement {
  const { name, control, label } = props;
  return (
    <Controller
      control={control}
      name={name}
      render={({
        field: { onChange, value },
        fieldState: { error },
        formState,
      }) => <TextField label={label} />}
    />
  );
}

export default function PropertyEditForm() {
  const { propertyId } = useParams();
  const { property } = usePropertyFetch(
    `${BASE_API_URL}/properties`,
    propertyId,
  );
  //  const { register, handleSubmit, reset, control, setValue } = useForm();
  const { control } = useForm();
  const address = property?.address;

  return (
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline />
      <Container>
        <Paper>
          <Typography variant="h3"> {property?.name}</Typography>
          {address && (
            <Typography>
              {address?.address} {address?.city}, {address?.state}{" "}
              {address?.zip}
            </Typography>
          )}
        </Paper>
        <Divider />
        <Paper sx={{ width: "75%" }}>
          <Typography variant="h4">Recent Projects</Typography>
        </Paper>
      </Container>
      <Divider />
      <Container>
        <Paper style={{ display: "grid", padding: "20px" }}>
          <FormInputText
            control={control}
            name={"description"}
            label={"Description"}
          />
          <FormInputText control={control} name={"address"} label={"Address"} />
          <FormInputText control={control} name={"city"} label={"City"} />
          <FormInputText control={control} name={"state"} label={"State"} />
          <FormInputText control={control} name={"zip"} label={"Zip"} />
        </Paper>
      </Container>
    </ThemeProvider>
  );
}
