import React from "react";
import {
  Button,
  ButtonGroup,
  Chip,
  Container,
  createTheme,
  CssBaseline,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Paper,
  ThemeProvider,
  Typography,
} from "@mui/material";
import { Link, useParams } from "react-router-dom";
import { usePropertyFetch } from "../hooks/PropertyFetch";
import { useProjectsQuery } from "../hooks/ProjectFetch";
import { Construction } from "@mui/icons-material";
import { idFromPath } from "../util/PathUtil";
import { BASE_API_URL } from "../util/Constants";

const defaultTheme = createTheme();

function ProjectList() {
  const { response } = useProjectsQuery(`${BASE_API_URL}/projects`);
  return (
    <List>
      {response?._embedded?.projects?.map((project, index) => (
        <ListItem
          key={index}
          secondaryAction={
            <ButtonGroup variant={"contained"}>
              <Button
                component={Link}
                to={`/projects/${idFromPath(project._links.self.href)}`}
              >
                View
              </Button>
              <Button>Edit</Button>
            </ButtonGroup>
          }
        >
          <ListItemIcon>
            <Construction />
          </ListItemIcon>
          <Chip
            label={project.status}
            variant={"filled"}
            style={{ marginRight: "1rem" }}
          />
          <ListItemText
            primary={project.description}
            primaryTypographyProps={{ style: { whiteSpace: "normal" } }}
          />
        </ListItem>
      ))}
    </List>
  );
}

export default function PropertyView() {
  const { propertyId } = useParams();
  const { property } = usePropertyFetch(
    `${BASE_API_URL}/properties`,
    propertyId,
  );
  const address = property?.address;
  return (
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline />
      <Container>
        <Paper>
          <Typography variant="h3"> {property?.name}</Typography>
          {address && (
            <Typography>
              {address?.address} {address?.city}, {address?.state}{" "}
              {address?.zip}
            </Typography>
          )}
        </Paper>
        <Divider />
        <Paper sx={{ width: "75%" }}>
          <Typography variant="h4">Recent Projects</Typography>
          <ProjectList />
        </Paper>
      </Container>
    </ThemeProvider>
  );
}
