import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Grid,
  Typography,
} from "@mui/material";
import React from "react";
import { Property } from "../hooks/PropertyFetch";
import { Link } from "react-router-dom";
import { idFromPath } from "../util/PathUtil";
import { DEFAULT_IMAGE_URL } from "../util/Constants";

export function PropertyCard(props: { property: Property }) {
  return (
    <Grid item>
      <Card sx={{ height: "100%", display: "flex", flexDirection: "column" }}>
        <CardContent sx={{ flexGrow: 1 }}>
          <CardMedia
            component="div"
            sx={{
              // 16:9
              pt: "56.25%",
            }}
            image={DEFAULT_IMAGE_URL}
          />
          <Typography gutterBottom variant="h5" component="h2">
            {props.property.name}
          </Typography>
          {props.property.address ? (
            <div>
              <Typography>{props.property.address?.address}</Typography>
              <Typography>
                {`${props.property.address?.city}, ${props.property.address?.state} ${props.property.address?.zip}`}
              </Typography>
            </div>
          ) : (
            <Typography>No address</Typography>
          )}
          <CardActions>
            <Button
              size="small"
              component={Link}
              to={`/properties/${idFromPath(props.property._links.self.href)}`}
            >
              View
            </Button>
            <Button
              size="small"
              component={Link}
              to={`/properties/edit/${idFromPath(
                props.property._links.self.href,
              )}`}
            >
              Edit
            </Button>
          </CardActions>
        </CardContent>
      </Card>
    </Grid>
  );
}
